<template>
    <div class="gerenmsg">
      <mt-header title="个人信息" id="header">
        <router-link to="/" slot="left" >
          <mt-button icon="back"></mt-button>
        </router-link>
      </mt-header>
      <div class="head " >
            <div class="icon " >
                 <i class="iconfont icon-user"></i>
            </div>
            <h2 id="sun">孙德龙</h2>
        <mt-button type="primary" id="btn" @click="back">退出登陆</mt-button>
      </div>
      <div class="content">
        <router-link to="/clientmsg">
          <mt-cell title="我的客户" value=">" class="item"></mt-cell>
        </router-link>
        <router-link to="/mymoney">
          <mt-cell title="我的余额" value=">" class="item"></mt-cell>
        </router-link>
        <router-link to="/zhucekehu">
          <mt-cell title="注册客户" value=">" class="item"></mt-cell>
        </router-link>
        <router-link to="/changepwd">
          <mt-cell title="修改密码" value=">" class="item"></mt-cell>
        </router-link>

      </div>
    </div>
</template>

<script>
  import axios from 'axios'
    export default {

        name: "gerenmsg",
        methods:{
          back(){
            let that = this
            axios.post('/api/user/logout', {
             // code:'200',
             //  data:'success',
             //  msg:'success'
            },{withCredentials: true})
              .then(function (response) {
                console.log(response);
                if(response.data.ret){
                  that.$router.push('/mydidi')
                }
              })
              .catch(function (error) {
                console.log(error);
              });
          }
        },
      mounted(){
        let that = this
        // console.log(11111);
        axios.get('/api/user/isLogin',{withCredentials: true})
          .then(function (response) {
            if(!response.data.ret){

              that.$router.push('/mydidi')
            }else{

              that.$router.push('/gerenmsg')
            }
          })
          .catch(function (error) {
            console.log(error);
          });
      }
    }
</script>

<style scoped>
  a{
    text-decoration: none;
    color: #000;
  }
  .clearfix::after{
    content: "";
    display: table;
    clear: both;
  }
  .gerenmsg .head{
    padding-top: 15px;
    height: 200px;
    background: url("../images/msg.png") no-repeat ;
    background-size: 110%;
    text-align: center;
  }
  .gerenmsg .head .icon{
    width:90px;
    height: 90px;
    border-radius: 50%;
    margin: 0px auto;
    /*background: url("../images/color.png") no-repeat;*/
    background: url("../images/color.png");
  }
  .gerenmsg .head .icon i{
    font-size: 80px;
  }
  #sun{
    font-size: 25px;
  }
  #btn{
    margin-top: 8px;
  }
 span.mint-cell-text{

 }
</style>



